/* ===================== HEADER ======================= */
.header {
    padding-top: 50px;
  }
  
  .rotate {
    -moz-transform: rotate(180deg) translateY(-3px);
    -webkit-transform: rotate(180deg) translateY(-3px);
    -o-transform: rotate(180deg) translateY(-3px);
    transform: rotate(180deg) translateY(-3px);
    display: inline-block;
  }