/* ===================== MENU ================== */
.wrapper-menu {
    width: 22px;
    height: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    cursor: pointer;
    transition: transform 330ms ease-out;
    position: relative;
    z-index: 9;
  }
  
  .open .wrapper-menu {
    transform: rotate(-45deg);
  }
  
  .line-menu {
    background-color: #111111;
    border-radius: 5px;
    width: 100%;
    height: 2px;
  }
  
  .line-menu.half {
    width: 50%;
  }
  
  .line-menu.start {
    transition: transform 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
    transform-origin: right;
  }
  
  .open .line-menu.start {
    transform: rotate(-90deg) translateX(3px);
  }
  
  .line-menu.end {
    align-self: flex-end;
    transition: transform 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
    transform-origin: left;
  }
  
  .open .line-menu.end {
    transform: rotate(-90deg) translateX(-3px);
  }
  
  /* ===================== MENU + OVERLAY ================== */
  
  .menu-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #ffffff;
    opacity: 0;
    z-index: 2;
    overflow: hidden;
    height: 0;
  }
  .open .menu-overlay {
    opacity: 1;
    height: auto;
    transition: all .5s;
    animation-timing-function: cubic-bezier(.57,.21,.69,1.25);
  }
  
  .menu-overlay nav a {
    font-family: PlayfairDisplay;
    font-size: 32px;
    padding: 0 30px;
    position: relative;
    color: #111111;
    transition: all 300ms ease-in-out;
    opacity: 0;
  }
  .open .menu-overlay nav a {
    opacity: 1;
    transition: opacity 1.5s cubic-bezier(.57,.21,.69,1.25);
  }
  .menu-overlay nav:hover a {
    color: #9c9c9c;
  }
  .menu-overlay nav:hover a::before {
    background: #9c9c9c;
  }
  .menu-overlay nav a:hover {
    text-decoration: none;
    color: #111111;
  }
  .menu-overlay nav a::before {
    content: '';
    width: 7px;
    height: 7px;
    display: inline-block;
    background: #111111;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -o-border-radius: 50%;
    border-radius: 50%;
    position: absolute;
    left: 0;
    top: 50%;
  }
  .menu-overlay nav a:hover::before {
    background: #111111;
  }
  .menu-overlay nav a:first-child::before {
    display: none;
  }