.projectList-wrapper .card {
    cursor: pointer;
    min-height: 500px;
    border: 0 none;
    margin-bottom: 40px;
    padding: 40px;
    position: relative;
    display: flex;
    justify-content: center;
    transition: all 0.4s ease-in-out;
    -webkit-transition: all 0.4s ease-in-out;
    -moz-transition: all 0.4s ease-in-out;
    -o-transition: all 0.4s ease-in-out;
    /* animation-name: appearence;
    animation-duration: 2s;
    animation-timing-function: ease-in-out;
    animation-delay: .5s; */
}
.projectList-wrapper .col-6[data-attr*= 'digi-products'] .card {
    background-color: #ddeefe;
}
.projectList-wrapper .col-6[data-attr*= 'mobile-apps'] .card {
    background-color: #d9f6f1;
}
.projectList-wrapper .col-6[data-attr*= 'websites'] .card {
    background-color: #f9f7c7;
}
.projectList-wrapper .card:hover {
    background-color: transparent !important;
}
.projectList-wrapper .card:hover figcaption {
    opacity: 1;
    -webkit-transition: all 0.4s ease-in-out;
    -moz-transition: all 0.4s ease-in-out;
    -o-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
}
.projectList-wrapper figcaption {
    opacity: 0;
    -webkit-transition: all 0.4s ease-in-out;
    -moz-transition: all 0.4s ease-in-out;
    -o-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
}
.projectList-wrapper figcaption h2{
    font-size: 32px;
    margin-bottom: 5px;
}
.projectList-wrapper figcaption .category {
    color: #9c9c9c;
    font-size: 18px;
}